

import foot from './../../common/footer/footer';
import heade from './../../common/header/header';
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XTable from 'vux/src/components/x-table/index.vue'

import MeScroll from 'mescroll.js'
import 'mescroll.js/mescroll.min.css'
export default {
name:'searc',
components:{
heade,
foot,
Group,
Datetime,
Selector,
Search,
Divider,
XButton,
Flexbox,
FlexboxItem,
XTable
},
data(){
return{
startDate:'',
endDate:'',
list:[
{key:'',value:'全部'},
{key:'1',value:'自由'},
{key:'2',value:'审批'},
{key:'3',value:'提交'},
{key:'4',value:'关闭'},
{key:'5',value:'作废'}
],
defaultValue:'',
flowList:[
{key:'',value:'全部'},
{key:'0001AV100000000G8TM3',value:'酒祖费用发货流程'},
{key:'0001AV100000000G8TM5',value:'绵柔M系列费用发货流程'},
{key:'0001AV100000000G8TMB',value:'绵柔其他费用发货流程'},
{key:'0001AV100000000G8TMP',value:'一斤半费用发货流程'},
{key:'0001AV100000000IS6KV',value:'杜一费用发货流程'},
{key:'0001AV100000000G8TLY',value:'现金发货流程'},
{key:'0001AV100000000G8TMS',value:'其他费用发货流程'}
],
flowValue:'',
mescroll: null, //mescroll实例对象
dataList:[], //列表数据
isShow:false,
Tday:''
}
},
methods:{

tj(){               //查询方法
if(this.startDate && this.endDate){      //查询条件不能为空
if(!this.isShow && this.mescroll){   //当查询条件变化时
this.isShow = true

this.mescroll.resetUpScroll();

this.mescroll.hideTopBtn();
}else if(this.mescroll == null){
this.isShow = true
this.mescroll = new MeScroll("mescroll", { //在vue的mounted生命周期初始化mescroll,确保此处配置的id能够被找到
up: {

callback: this.upCallback,
toTop: { //配置回到顶部按钮
src: require('@/assets/image/mescroll-totop.png') 
},

empty:{
warpId : 'mescroll' ,
icon : null ,
tip : "暂无订单数据,请重新输入查询条件~" ,
btntext : "" ,
btnClick : null,
supportTap : false
},

noMoreSize:1,


hardwareClass:"mescroll-hardware",


htmlLoading:'<p class="upwarp-progress mescroll-rotate"></p><p class="upwarp-tip">加载中..</p>',


htmlNodata:'<p class="upwarp-nodata"> -- 加载完毕 -- </p>',


auto:true,
},
down: {
use:false
}

});
}
}else{
this.$vux.alert.show({
title: '注意',
content: '查询条件不能为空',
})
}

},
upCallback(page) {

this.$axios.get(this.url+'/ncPreorderServiceController.do?datagrid', {
params: {
num: page.num, //页码
size: page.size, //每页长度
sessionid:localStorage.sessionid,
dbilldate_begin:this.startDate,  //订单开始日期
dbilldate_end:this.endDate,    //订单结束日期
businame:this.flowValue, // 流程名称
fstatus:this.defaultValue  
}
}).then((response)=> {

let arr = response.data.rows;

if (page.num == 1) this.dataList = [];

this.dataList = this.dataList.concat(arr);

this.$nextTick(() => {
this.mescroll.endSuccess(arr.length);
})

}).catch((e)=> {

this.mescroll.endErr();
})

},

dj(id){     // 点击订单号跳转到订单详情
this.$vux.loading.show({})
this.$axios.get(this.url+'/ncPreorderServiceController.do?preorderdetaildatagrid', {     //调用提交接口
params: {
sessionid:localStorage.sessionid,
id:id
}
}).then((res)=> {
this.$vux.loading.hide()
this.$store.commit('products',res.data.rows)
this.$router.push({name:'orderDetail'})
}).catch((e)=> {
this.$vux.loading.hide()
this.$vux.alert.show({
title: '注意',
content: '服务器异常,请稍后再试!',
})
})
},
change(id){
this.$vux.loading.show({})
this.$axios.get(this.url+'/ncPreorderServiceController.do?preorderheaddatagrid', {     //调用经销商提交接口
params: {
sessionid:localStorage.sessionid,
id:id
}
}).then((res)=> {
this.$store.commit('changeDetail',res.data.rows[0]);
this.$axios.get(this.url+'/ncPreorderServiceController.do?preorderdetaildatagrid', {     //调用提交接口
params: {
sessionid:localStorage.sessionid,
id:id
}
}).then((res)=> {
this.$vux.loading.hide()
this.$store.commit('rental',res.data.rows);
this.$store.commit('whichId',id)
this.$router.push({name:'changeOrder'});
}).catch((e)=> {
this.$vux.loading.hide()
this.$vux.alert.show({
title: '注意',
content: '服务器异常,请稍后再试!',
})
})
}).catch((e)=> {
this.$vux.alert.show({
title: '注意',
content: '服务器异常,请稍后再试!',
})
})
}

},
watch:{              //监听查询条件的变化
flowValue(val,oldValue){
if(val != oldValue){
this.isShow = false;
}
},
defaultValue(val,oldValue){
if(val != oldValue){
this.isShow = false;
}
},
startDate(val,oldValue){
if(val != oldValue){
this.isShow = false;
}
},
endDate(val,oldValue){
if(val != oldValue){
this.isShow = false;
}
}
},
created(){
this.$store.commit('keepAlive','searc')
let dayN = new Date()  
let year = dayN.getFullYear()
let mon = dayN.getMonth()
if(mon+1 < 10){
mon = '0' + (mon+1)
}else{
mon = (mon+1)
}
let day = dayN.getDate()
if(day < 10){
day = '0' + day
}
let Tday = year + '-' + mon + '-' + day
this.Tday = Tday
this.startDate = Tday
this.endDate = Tday
},
beforeRouteLeave (to, from, next) {
if(to.path == '/changeOrder'){
this.$store.commit('changekeepAlive','changeOrder')
}
next()
},
}




